.TableHeaderContainer{
   
    border-radius: 10px;
}
.chartNameHeaderContainer {
    box-sizing: border-box;
    padding:30px 10px 0px 10px;
    width: 100%;
    justify-content: space-between;
}
.flexVerticallyCenterSpaceBetween {
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.center{
    padding: 20px 10px;
    text-align: left;
}
.visualizeIcon {
    padding:10px
}
.chartNameInfo{
    font-size: 12px;
    color: grey;
}
.chartNameLeft {
    width: 100%;
    max-width: 300px;
    justify-content: space-between;
}
.ChartConfigContainer {
    font-size: 14px;
    padding: 5px 20px;
    background-color: #F0F2F5;
}
.chartConfigLeft > div {
    color: #5664D2;
    padding: 10px;
    font-size:13px;
    font-weight:600
}

.dataFrameContainer {
    margin: 0px 30px;
    padding: 10px ;
    border-bottom: 1px solid lightgrey;
    
}

.dataFramesIcon {
    font-size: 10px;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    padding: 5px;
    background: #0000e1;
    background-image: linear-gradient(#18AFFF, #0062DF);
    margin: 0px 10px;
}
  
.dataFrameContainerLeft{
    font-size: 15px;
    color: #65676B
  }

  .generateChartButton {
    background-color: #0b6ae3;
    border: none;
    cursor: pointer;
    color: white;
    font-weight: 600;
    border-radius: 5px;
    padding: 10px;
    background-image: linear-gradient(#18AFFF, #0062DF)
  }
  .generateChartButton:hover {
    background-image: linear-gradient( #0062DF, #0264e5);
  }